import { Link as RouterLink, useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';
import {
  Box,
  Button,
  Link,
  TextField,
  Typography
} from '@material-ui/core';
import { useCookies } from 'react-cookie';
import * as Yup from 'yup';
import { Formik } from 'formik';
import APIService from '../../apiservices/APIService'

const LoginForm = () => {
  const [loginError, setLoginError] = useState('')
  const [token, setToken] = useCookies(['mytoken']);
  const navigate = useNavigate();
  useEffect(() => {
    if (token.mytoken) {
      navigate('/front/dashboard');
    }
  }, [token]);
  const loginBtn = (email, password, actions) => {
    APIService.LoginUser(email, password)
      .then((resp) => {
        if (resp.token == null) throw Error(resp);
        setToken('mytoken', resp.token)
      })
      .catch((error) => {
        console.log(error)
        setLoginError('Ошибка авторизацииб попробуйте позднее')
        actions.setSubmitting(false)
      })
  }

  return (
    <Formik
      initialValues={{
        email: '',
        password: ''
      }}
      validationSchema={Yup.object().shape({
        email: Yup.string().email('Must be a valid email').max(255).required('Email is required'),
        password: Yup.string().max(255).required('Password is required')
      })}
      onSubmit={(values, actions) => {
        loginBtn(values.email, values.password, actions)
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        touched,
        values
      }) => (
        <form onSubmit={handleSubmit}>
          <Box sx={{ mb: 3 }}>
            <Typography
              color="textPrimary"
              variant="h2"
            >
              Авторизация
            </Typography>
            <Typography
              color="textSecondary"
              gutterBottom
              variant="body2"
            >
              Авторизуйтесь для продолжения работы
            </Typography>
          </Box>
          <TextField
            error={Boolean(touched.email && errors.email)}
            fullWidth
            helperText={touched.email && errors.email}
            label="Email Address"
            margin="normal"
            name="email"
            onBlur={handleBlur}
            onChange={handleChange}
            type="email"
            value={values.email}
            variant="outlined"
          />
          <TextField
            error={Boolean(touched.password && errors.password)}
            fullWidth
            helperText={touched.password && errors.password}
            label="Password"
            margin="normal"
            name="password"
            onBlur={handleBlur}
            onChange={handleChange}
            type="password"
            value={values.password}
            variant="outlined"
          />
          <Box sx={{ py: 2 }}>
            <Button
              color="primary"
              disabled={isSubmitting}
              fullWidth
              size="large"
              type="submit"
              variant="contained"
            >
              Sign in now
            </Button>
          </Box>
          <Typography
            color="textSecondary"
            variant="body1"
          >
            Если нет аккаунта, то зарегистрируйся
            {' '}
            <Link component={RouterLink} to="/register" variant="h6" underline="hover">
              Sign up
            </Link>
          </Typography>
          <Typography
            color="common.red"
          >
            {loginError}
          </Typography>
        </form>
      )}
    </Formik>
  );
};

export default LoginForm;
