export default class APIService {
  static GetUsers(token) {
    return fetch('/app/appusers', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Token ${token}`
      }
    }).then((resp) => resp.json())
  }

  static UpdateArticle(articleId, body, token) {
    return fetch(`http://127.0.0.1:8000/api/articles/${articleId}/`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Token ${token}`
      },
      body: JSON.stringify(body)
    }).then((resp) => resp.json())
  }

  static InsertArticle(body, token) {
    return fetch('http://127.0.0.1:8000/api/articles/', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Token ${token}`
      },
      body: JSON.stringify(body)
    }).then((resp) => resp.json())
  }

  static DeleteArticle(articleId, token) {
    return fetch(`http://127.0.0.1:8000/api/articles/${articleId}/`, {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Token ${token}`
      }
    })
  }

  static LoginUser(email, password) {
    return fetch('http://127.0.0.1:8000/auth/', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: `{"username":"${email}","password":"${password}"}`
    }).then((resp) => resp.json())
  }

  static RegisterUser(body) {
    return fetch('http://127.0.0.1:8000/auth/users/', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(body)
    }).then((resp) => resp.json())
  }
}
