import { Link as RouterLink, useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';
import { useCookies } from 'react-cookie';
import * as Yup from 'yup';
import { Formik } from 'formik';
import {
  Box,
  Button,
  Checkbox,
  FormHelperText,
  Link,
  TextField,
  Typography
} from '@material-ui/core';
import APIService from '../../apiservices/APIService'

const RegistrationForm = () => {
  const [loginError, setLoginError] = useState('')
  const [token, setToken] = useCookies(['mytoken']);
  const navigate = useNavigate();
  useEffect(() => {
    if (token.mytoken) {
      navigate('/front/dashboard');
    }
  }, [token]);
  const loginBtn = (email, password, actions) => {
    APIService.RegisterUser({email, password})
      .then((resp) => setToken('mytoken', resp.token))
      .catch((error) => {
        console.log(error)
        setLoginError('Ошибка авторизацииб попробуйте позднее')
        actions.setSubmitting(false)
      })
  }

  return (
    <Formik
      initialValues={{
        email: '',
        firstName: '',
        lastName: '',
        password: '',
        policy: false
      }}
      validationSchema={
        Yup.object().shape({
          email: Yup.string().email('Must be a valid email').max(255).required('Email is required'),
          password: Yup.string().max(255).required('password is required'),
          policy: Yup.boolean().oneOf([true], 'This field must be checked')
        })
      }
      onSubmit={(values, actions) => {
        loginBtn(values.email, values.password, actions)
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        touched,
        values
      }) => (
        <form onSubmit={handleSubmit}>
          <Box sx={{ mb: 3 }}>
            <Typography
              color="textPrimary"
              variant="h2"
            >
              Создать новый аккаунт
            </Typography>
            <Typography
              color="textSecondary"
              gutterBottom
              variant="body2"
            >
              Используйте свой email для нового аккаунта
            </Typography>
          </Box>
          <TextField
            error={Boolean(touched.email && errors.email)}
            fullWidth
            helperText={touched.email && errors.email}
            label="Email Address"
            margin="normal"
            name="email"
            onBlur={handleBlur}
            onChange={handleChange}
            type="email"
            value={values.email}
            variant="outlined"
          />
          <TextField
            error={Boolean(touched.password && errors.password)}
            fullWidth
            helperText={touched.password && errors.password}
            label="Password"
            margin="normal"
            name="password"
            onBlur={handleBlur}
            onChange={handleChange}
            type="password"
            value={values.password}
            variant="outlined"
          />
          <TextField
            error={Boolean(touched.password && errors.password)}
            fullWidth
            helperText={touched.password && errors.password}
            label="Repeat Password"
            margin="normal"
            name="repeatpassword"
            onBlur={handleBlur}
            onChange={handleChange}
            type="password"
            value={values.password}
            variant="outlined"
          />
          <Box
            sx={{
              alignItems: 'center',
              display: 'flex',
              ml: -1
            }}
          >
            <Checkbox
              checked={values.policy}
              name="policy"
              onChange={handleChange}
            />
            <Typography
              color="textSecondary"
              variant="body1"
            >
              Я ознакомился с правилами
              {' '}
              <Link
                color="primary"
                component={RouterLink}
                to="#"
                underline="always"
                variant="h6"
              >
                Terms and Conditions
              </Link>
            </Typography>
          </Box>
          {Boolean(touched.policy && errors.policy) && (
            <FormHelperText error>
              {errors.policy}
            </FormHelperText>
          )}
          <Box sx={{ py: 2 }}>
            <Button
              color="primary"
              disabled={isSubmitting}
              fullWidth
              size="large"
              type="submit"
              variant="contained"
            >
              Sign up now
            </Button>
          </Box>
          <Typography
            color="textSecondary"
            variant="body1"
          >
            Уже есть аккаунт?
            {' '}
            <Link component={RouterLink} to="/login" variant="h6" underline="hover">
              Sign in
            </Link>
          </Typography>
          <Typography
            color="common.red"
          >
            {loginError}
          </Typography>
        </form>
      )}
    </Formik>
  );
};

export default RegistrationForm;
